import React, {useEffect, useState} from "react";
import logo from '../assets/app-icon.svg'

interface ILoader {
    loading: boolean
}

const Loader = ({loading} :ILoader) => {
    const [className, setClassName] :any[] = useState('loader isActive')
    useEffect(() => setClassName('loader'), [loading])

    return (
    <div className={className}>
        <img src={logo} alt="" className="loader__image"/>
        <ul className="loader__helpers">
            {new Array(9).fill(0).map((_, i) => <li key={i}/>) }
        </ul>
    </div>)
}

export default Loader