import React, {useState} from "react";
import mail from '../assets/mail.svg'
import password from '../assets/password.svg'
import eyeOpen from '../assets/eye-open.svg'
import eyeClosed from '../assets/eye-closed.svg'

import styles from '../style/components/login.module.scss';

interface IInput {
    type: string
    setInputValues: (state: any) => void
    id: string
    placeholder: string
    error: string
}

const Input = ({type, setInputValues, id, placeholder, error}: IInput) => {
    const [eye, setEye] :any = useState(false)
    const [dinamicType, setDinamicType] = useState(type)

    const onChangeHandler = (e: any) => {
        if (type === 'password') {
            setEye(false)
            setDinamicType('password')
        }
        setInputValues((state: any) => {
            return {
                ...state,
                [e.target.name]: e.target.value
            }
        })
    }

    const typeChange = () => {
        setEye(!eye)
        setDinamicType(eye ? 'password' : 'text')
    }

    return (
        <div className={`${styles['login__input']} ${error ? styles['login__input--invalid'] : ''}`}>
            <img className={styles['login__input-icon']} src={id === 'email' ? mail : password} alt=""/>

            <input className={`${styles['login__input-body']}`}
                   id={id}
                   name={id}
                   type={dinamicType}
                   placeholder={placeholder}
                   onChange={onChangeHandler} />

            {type === 'password' ? <img onClick={typeChange} className={styles['login__input-eye']} src={eye ? eyeOpen : eyeClosed} alt="" /> : <></>}

            {error ? <div className={styles['login__input-error']}>{error}</div> : <></>}
        </div>
    )
};

export default Input