import React, {useEffect, useState} from 'react';
import logo from '../assets/logo.svg'
import Loader from "./Loader";
import Input from "./Input";
import Messages from "./Messages";

import styles from '../style/components/login.module.scss';

const validateEmail = (email :string) :boolean => {
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  return re.test(String(email).toLowerCase())
}

const Login = () => {
  let loading :boolean = true
  const [loader, setLoader] :any[] = useState(true)
  const [btnDisabled, setBtnDisabled] :any[] = useState(true)
  const [inputValues, setInputValues] :any[] = useState({email: '', password: ''})
  const [errors, setErrors] :any[] = useState({email: '', password: ''})
  const [messages, setMessages] :any[] = useState()

  useEffect(() => {
    if (inputValues.email && inputValues.password) setBtnDisabled(false)
    else if (!inputValues.email && !inputValues.password) setBtnDisabled(true)
    setErrors({email: '', password: ''})
  }, [inputValues])

  useEffect(() => {
    loading = false
    setTimeout(() => setLoader(false), 300)
  }, [])

  const submitHandler = (e: any) => {
    e.preventDefault()
    setErrors({password: '', email: ''})
    if (btnDisabled) return

    const formData = {
      login: inputValues.email,
      password: inputValues.password,
    }

    if (!validateEmail(formData.login)) {
      setErrors((state: any) => {
        return {
          ...state,
          email: 'Incorrect mail'
        }
      })
    }

    if (formData.password.length < 8) {
      setErrors((state: any) => {
        return {
          ...state,
          password: 'The minimum password length is 8 characters'
        }
      })
    }

    if (!validateEmail(formData.login) || formData.password.length < 8) return

    setMessages({
      type: 'error',
      text: 'Email or Pasword error'
    })

    console.log(formData)
  }

  return !loader ? (
    <form className={styles['login']} onSubmit={submitHandler} autoComplete="off">
      <div className={styles['login__header']}>
        <img src={logo} alt=""/>
      </div>

      <h1 className={styles['login__title']}>Hello!</h1>
      <h2 className={styles['login__supTitle']}>Log in to your account</h2>

      <div className={styles['login__input-wrapper']}>
        <Input type='text' setInputValues={setInputValues} id='email' placeholder='E-Mail' error={errors.email}/>
        <Input type='password' setInputValues={setInputValues} id='password' placeholder='Password' error={errors.password}/>

        <button className={`${styles['login__input-submit']} ${btnDisabled ? styles['disabled'] : ''}`} type='submit'>LOG IN</button>
      </div>

      <Messages messages={messages} setMessages={setMessages} />
    </form>
  ) : <Loader loading={loading}/>
}

export default Login;
