import React, {useEffect, useState} from "react";
import styles from '../style/components/messages.module.scss'

interface IMessages {
  messages: any
  setMessages: (p: () => undefined, number: number) => void
}

function Messages({messages, setMessages}: IMessages) {
  let [hide, setHide] :any = useState(true)

  useEffect(() => {
    setHide(!messages)
  }, [messages])

  const hideHandler = () => {
    setHide(true)
    setTimeout(() => setMessages((() :undefined => undefined), 300))
  }

  setTimeout(() => hideHandler(), 7 * 1000)

  return messages ? (
    <div className={`${styles['message']} ${styles[messages.type]} ${hide ? styles['hide'] : ''}`}>
      <b>{messages.text}</b>
      <svg onClick={hideHandler} width="14" height="14" viewBox="0 0 14 14" xmlns="http://www.w3.org/2000/svg">
        <path
          d="M14 1.41L12.59 0L7 5.59L1.41 0L0 1.41L5.59 7L0 12.59L1.41 14L7 8.41L12.59 14L14 12.59L8.41 7L14 1.41Z"/>
      </svg>
    </div>) : <></>
}

export default Messages